.footer-container {
  box-shadow: 0px 0px 5px 0px #cdcdcd;
  background-color: #343a40;
  min-height: 200px;
  color: #fff;
  font-size: 15px;
}

.footer-container .footer-grid-top {
  margin: 15px 0px;
}

.footer-container .footer-grid-top .footer-company {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.footer-container .footer-grid-top .footer-company h2 {
  text-shadow: 4px 4px 2px #282d31;
}

.footer-container .footer-grid-bottom {
  background-color: #282d31;
  padding: 20px;
}
