.main-container {
  width: 100%;
  height: auto;
  background-color: #00aeef;
  color: #fff; }
  .main-container h1 {
    width: 100%;
    font-size: 35px;
    text-align: center; }
  .main-container .main-banner {
    padding: 120px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .main-container span {
    text-align: center;
    font-weight: bold;
    background-color: #028bbd;
    padding: 15px;
    border-radius: 150px; }

.main-features {
  margin: 5px;
  color: #5e707d; }

@media (max-width: 991px) {
  .main-container h1 {
    font-size: 15px; }
  .main-container .main-banner {
    padding: 100px 0px; } }

